.site-content {
  position: relative;
  background-color: #181818;
  padding: 3.375rem 0 0;
}
.site-content:before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3.375rem;
  background: linear-gradient(#0b0b0b, rgba(11, 11, 11, 0));
}
.page-template-template-content-grouping .site-content:before {
  z-index: 1;
