  padding-left: 1.5rem;
}
.link._back:before, .view-back-to-search a:before, ._back.friendship-button.add:before, .view-back-to-search a.friendship-button.add:before {
  content: "";
  display: block;
  width: 0.5rem;
  height: 0.5rem;
  position: absolute;
  top: 0;
  left: 0;
  border-width: 2px 0 0 2px;
  border-style: solid;
  border-color: #FDD80F;
  border-radius: 2px 0 0 0;
  transform-origin: center;
  transform: translateY(50%) rotate(-45deg);
