.nabcard {
  position: relative;
  margin: 2rem auto 3rem;
  max-width: 79.8125rem;
}
.nabcard__content {
  background-color: #181818;
  position: relative;
  z-index: 2;
  border-radius: 8px;
  padding: 2rem;
}
.nabcard:before {
  content: "";
  display: block;
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 8px;
  background: #0b0b0b;
  transform-origin: center;
  transform: rotate(-0.5deg);
}
@media (min-width: 48em) {
  .nabcard:before {
    transform: rotate(-1deg);
    /*content:"768px";*/
  }
}
@media (min-width: 64em) {
  .nabcard:before {
    transform: rotate(-2deg);
    /*content:"1024px";*/
  }
}

.radiolist {
  background-color: black;
  display: inline-block;
  list-style: none;
  padding: 2rem;
  margin: 0;
  box-shadow: 5px 10px 14px rgba(0, 0, 0, 0.08);
  border-radius: 5px;
}
.radiolist li {
  padding-bottom: 1.5rem;
  font-size: 1.125rem;
  font-family: "Roboto Mono", serif;
  color: #ffffff;
  line-height: 1.111;
}
.radiolist li:last-child {
  padding-bottom: 0;
}
.radiolist li .option {
  padding-left: 2.625rem;
  position: relative;
  line-height: 1.4375rem;
}
.radiolist li .option:before {
  content: "";
  display: block;
  background: black;
  border-radius: 50%;
  width: 1.4375rem;
  height: 1.4375rem;
  position: absolute;
  z-index: 2;
  top: calc(50% - 11.5px);
  left: 1px;
  transition: width 0.25s ease-in-out, height 0.25s ease-in-out, left 0.25s ease-in-out, top 0.25s ease-in-out;
}
.radiolist li .option:after {
  content: "";
  display: block;
  position: absolute;
  z-index: 1;
  top: calc(50% - 12.5px);
  left: 0;
  appearance: none;
  width: 1.5625rem;
  height: 1.5625rem;
  border-radius: 50%;
  background: linear-gradient(#ffffff, #ffffff);
  transition: background 0.25s ease-in-out;
}
.radiolist li .option._selected:before {
  width: 9px;
  height: 9px;
  top: calc(50% - 4.5px);
  left: 8px;
}
.radiolist li .option._selected:after {
  background: linear-gradient(90deg, #6A5FAA, #E3048A);
}

.showlist {
  position: relative;
}
.showlist__feedtype {
  display: inline-block;
  text-decoration: underline;
  text-transform: none;
  color: #FDD80F;
  cursor: pointer;
}
.showlist__feedtype:after {
  content: "";
  display: inline-block;
  width: 0;
  height: 0;
  border-color: #FDD80F transparent transparent transparent;
  border-style: solid;
  border-width: 0.4375rem 0.5rem 0 0.5rem;
  margin-left: 0.375rem;
}
.showlist__feedchooser {
  position: absolute;
  z-index: 30;
  left: 1.25rem;
  top: calc(100% - 20px);
  max-height: 0;
  padding: 0 2rem;
  overflow: hidden;
  transition: max-height 0.25s ease-in-out, padding 0.25s ease-in-out;
}
._open .showlist__feedchooser {
  max-height: 31.25rem;
  padding: 2rem;
}

.nabblock {
  border-left: 5px solid #6A5FAA;
  border-image: linear-gradient(0deg, #E3048A, #6A5FAA) 5;
  border-width: 0;
