.dropborder {
  display: inline-block;
  position: relative;
  margin: 1rem;
  border: 4px solid #6A5FAA;
  border-image: linear-gradient(90deg, #6A5FAA, #E3048A) 4;
}
.dropborder > * {
  display: block;
  z-index: 2;
  position: relative;
  top: -1rem;
  left: -1rem;
}

.textimage {
  position: relative;
}
.textimage a {
  text-decoration: none;
}
.textimage__image {
  position: relative;
  margin: 0.75rem 0 1rem 1rem;
  border: 4px solid #6A5FAA;
  border-image: linear-gradient(90deg, #6A5FAA, #E3048A) 4;
}
._topborder .textimage__image {
  border-width: 4px 0 0 0;
  margin: 0;
}
.textimage__image img {
  position: relative;
  display: block;
  left: -1rem;
  top: -0.75rem;
}
._topborder .textimage__image img {
  position: static;
}
.textimage__image:after {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 3;
  top: -0.75rem;
  left: -1rem;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.9) 10%, rgba(0, 0, 0, 0) 90%);
}
._topborder .textimage__image:after {
  top: 0;
  left: 0;
}
.textimage__text {
  text-decoration: none;
  color: #ffffff;
  transition: color 0.25s ease-in-out;
}
a:hover .textimage__text, a:focus .textimage__text, a:active .textimage__text {
  color: #FDD80F;
}
._topborder .textimage__text {
  position: absolute;
  z-index: 4;
  left: 0;
  right: 0;
  bottom: 1rem;
  padding: 0 1rem;
}
