  .textimage__text {
    position: absolute;
    z-index: 4;
    left: 0;
    right: 0;
    bottom: 1rem;
    padding: 0 2rem;
    /*content:"1024px";*/
  }
}
@media (min-width: 80em) {
  .textimage__text {
    bottom: 1.25rem;
    /*content:"1280px";*/
  }
}

