.search {
	&__form {
		background-color: $color__gray;
		height: rem(50);
		border-radius: rem(25);
		margin: 0 rem(24) rem(30);
		padding: 0 rem(14) 0 rem(15);
		display: flex;
		@include bp-768 {
			max-width: rem(650);
			margin: 0 auto rem(30);
		}
		@include bp-1024 {
			margin: 0 auto rem(40);
		}
	}
		&__field.st-default-search-input {
			background-color: transparent;
			background-image: url('/wp-content/themes/nab-amplify/assets/images/icon-search.svg');
			background-repeat: no-repeat;
			background-position: calc(100% - #{rem(4)}) rem(14);
			background-size: rem(22) rem(21);
			font-family: $font__secondary;
			font-size: rem(16);
			color: $color__white;
			display: block;
			width: 100%;
			height: auto;
			padding: 0;
			border: 0;
			font-weight: 400;
			line-height: rem(50);

			&::placeholder {
				color: $color__gray-lighter;
			}
		}
		&__submit {
			background-color: transparent;
			border: 0;
			cursor: pointer;
		}
	&__term {
	  color: $color__yellow;
	}
	&__sort {
	  list-style: none;
	  padding: 0;
	  margin: 2rem 0 0;
	  display: flex;
	  justify-content: center;
	  li {
		width: rem(200);
	  }
	}
	&__sortOption, .sort-order {
	  display: block;
	  width: rem(200);
	  height: rem(40);
	  line-height: rem(34);
	  text-align: center;
	  text-decoration: none;
	  color: $color__white;
	  font-size: rem(16);
	  font-weight: bold;
	  border: 3px solid $color__purple; //fallback
	  border-image: linear-gradient(90deg, $color__purple, $color__pink) 3;
	  &._selected, &.active {
		background: linear-gradient(90deg, $color__purple, $color__pink);
	  }
	}
  }

//misc classes from multidots
.nab-search-result-wrapper {
	display: flex;
	flex-flow: column nowrap;
	.search-result-filter {
		order: 1;
	}
	.search-view-top-head {
		order: 0;
	}
	.search-section {
		order: 2;
	}
	.load-more {
		order: 3;
	}
}
.search-section-heading {
	h2 {
		display: inline-block;
		@include bp-1024 {
			font-size: rem(44);
		}
		strong {
			font-family: $font__primary;
			font-weight: 600;
		}
		span {
			font-weight: 400;
			font-size: rem(30);
			vertical-align: top;
			color: $color__gray-lighter;
		}
	}
	.section-view-more {
		display: inline-block;
		padding-left: rem(20);
		position: relative;
		top: rem(-9);
	}
}

.other-search-filter {
	width: 100%;
	.sort-order-btn {
		display: flex;
		flex-flow: row wrap;
		justify-content: center;
	}
	.inline-filter-select-boxes {
		margin-top: rem(20);
		@include bp-768 {
			margin-top: 0;
			margin-left: rem(20);
		}
	}
	.filter-select-boxes {
		@include bp-768 {
			max-width: rem(700);
			margin-left: auto;
			margin-right: auto;
			display: flex;
			justify-content: space-between;
			&._multirows {
				flex-flow: row wrap;
			}
		}
		.nab-custom-select {
			margin-top: rem(20);
			@include bp-768 {
				padding: 0 rem(10);
				width: 100%;

			}
		}
		.filter_box_row {
			margin-top: rem(20);
			@include bp-768 {
				flex: 0 1 100%;
				display: flex;
				justify-content: space-between;
				& > div {
					padding: 0 rem(10);
					width: 100%;
				}
			}

			input[type=text] {
				background-color: $color__gray;
				color: $color__white;
				line-height: rem(38);
				border-radius: rem(19);
				margin-top: rem(20);
				font-size: rem(18);
				padding-left: rem(20);
				padding-right: rem(20);
				&::placeholder {
					color: $color__gray-lighter;
				}
				@include bp-768 {
					width: 100%;
				}
			}
		}
	}
}

.view-back-to-search {
	margin-top: rem(20);
	text-align: center;
	a {
		@extend .link, ._back;
		color: $color__yellow;
		font-weight: 600;
	}
}

.search-view-top-head {
	text-align: center;
	h2 {
		margin-top: .5em;
		margin-bottom: .5em;
		font-size: rem(26);
		line-height: rem(26);
		@include bp-1280 {
			font-size: rem(42);
			line-height: rem(42);
		}
		strong {
			font-weight: 600;
			color: $color__yellow;
		}
	}
	.view-top-other-info {
 		display: none;
 	}
}

.search-section {
	.nab-ad-wrap {
		padding: rem(40) 0;
	}
}

.result {
	position: relative;
	display: block;
	border-top: 3px solid $color__purple;
	border-image: linear-gradient(90deg, $color__purple, $color__pink) 3;
	border-width: 0;
	border-top-width: 3px;
	background-color: $color__gray-darkest;
	padding: rem(18) rem(18) rem(46);
	height: 100%;
	text-align: center;
	text-decoration: none;
	&._content {
		padding: 0 0 rem(36);
		a {
			text-decoration: none;
		}

	}
	.amp-bookmark {
		position: absolute;
		top: rem(10);
		left: rem(10);
		color: $color__pink;
	}
	&__hide {
		//hide items in search results due to old styling
		display: none;
	}

	&__imgLink {
		._content & {
			position: relative;
			display: block;
			height: rem(140);
			overflow: hidden;
			background-color: $color__white;
			.result__image {
				position: absolute;
				left: 0;
				top: 50%;
				transform: translateY(-50%);
				color: $color__black;
			}
		}
	}
	&__image, &__imageWrap img {
	  border-radius: 50%;
	  max-width: rem(102);
	  ._content & {
	  	border-radius: 0;
	  	max-width: 100%;
	  }
	}
	&__title {
	  font-weight: 700;
	  font-size: rem(22);
	  line-height: rem(27);
	  min-height: rem(54);
	  color: $color__white;
	  a, a & {
	  	text-decoration: none;
	  	color: $color__white;
	  	transition: color .25s ease-in-out;
	  	&:hover, &:focus, &:active {
	  		color: $color__yellow;
	  	}
	  }
	  ._content & {
	  		padding: rem(24) rem(18) 0;
	  	 	margin-bottom: 0;
	  	 	overflow: hidden;
	  	 	max-height: rem(105);
	  	 	background:
	  	 		linear-gradient(to top, $color__gray-darkest #{rem(26)}, rgba($color__gray-darkest, 0) #{rem(32)}),
	  	 		url('/wp-content/themes/nab-amplify/assets/images/ellipsis.min.svg');
	  	 	background-repeat: no-repeat;
	  	 	background-size: 100% 100%, rem(16) auto;
	  	 	background-position: 0 0, calc(100% - #{rem(5)}) calc(100% - #{rem(7)});
	  	 	background-attachment: local, scroll;
	  }
	  // ._content:hover &, ._content:focus &, ._content:active & {
	  // 	color: $color__yellow;
	  // }

	}
	&__desc {
		padding: rem(24) rem(18) 0;
	}
	&__button {
	  margin-bottom: rem(27);
	  ._content & {
	  	margin-top: rem(20);
	  }
	}
	&__message {
	  display: block;
	  font-size: rem(18);
	  font-weight: 600;
	}
  }
